енных в рассматриваемой предметной области. Модель включает взаимосвязанные концепты (сущности), их свойства и характеристики. Предполагается классификация компонентов по типам, ситуациям, ограничениям целостности. Исходя из концептуальной модели строится логическая модель базы данных.")]),_c('p',[_vm._v("Концептуальная модель – это информационная модель предметной области, включающая классификацию данных, их структуризацию и семантическую целостность (в том числе достоверность и согласованность данных).")]),_c('p',[_vm._v("Наиболее известна концептуальная модель “сущность-связь”. В ней два основных объекта – сущности и связи. Не все связи реализуются напрямую в логической модели.")]),_c('h3',[_vm._v("4. Семиотика. Прагматики и семантики данных")]),_c('br'),_c('p',[_vm._v("Семио́тика, в лингвистике, философии – семиоло́гия")]),_c('p',[_vm._v("(от греческого σημειωτική, от др.-греч. σημεῖον — «знак, признак») наука о коммуникативных системах и знаках, используемых в процессе общения (Лотман Ю.М.).")]),_c('p',[_vm._v("Три основных аспекта изучения знака и знаковой системы:")]),_c('p',[_vm._v("синтаксис (синтактика) изучает внутренние свойства систем знаков безотносительно к интерпретации знака или системы знаков; синтактика включает синтагматику (это сочетаемость знаков) и парадигматику (строение языковых единиц)")]),_c('p',[_vm._v("семантика (в лингвистике семасиология) определяет отношение знаков к обозначаемому объекту (см. денотат и десигнат);")]),_c('p',[_vm._v("прагматика определяет интерпретацию знака в зависимости от точки зрения, выбранного аспекта знака, приемлемости, полезности или ценности знака для интерпретатора.")]),_c('p',[_vm._v("Денотат единицы языка — это множество объектов действительности именуемых этой единицей. Соответствует термину “объём понятия” в классической логике.")]),_c('p',[_vm._v("Десигнат — означаемое, то есть, значение слова. Противопоставляется денотату как “субъективный образ” или “концепт” денотата. Понятия могут иметь десигнат, но не иметь денотата. Примеры: “русалка”, ”круглый квадрат“. ")]),_c('p',[_vm._v("Как писал Ч.Моррис, “Знаки, указывающие на один и тот же объект, не обязательно имеют те же самые десигнаты, поскольку то, что учитывается в объекте, у разных интерпретаторов может быть различным” [“Основания теории знаков”].")]),_c('p',[_vm._v("В базах данных, как правило, денотат определяется однозначно, а десигнат может варьировать исходя из выбранной прагматики.")]),_c('h3',[_vm._v("Синтаксис, семантики и прагматики")]),_c('img',{staticStyle:{"width":"600px"},attrs:{"src":require("../assets/4.jpg")}}),_c('br'),_c('br'),_c('h3',[_vm._v("Какие виды семантик мы будем изучать")]),_c('br'),_c('p',[_vm._v("Сначала вспомним, что такое активность. Будем говорить, что база данных активна, если она выполняет не только действия явно предписанные при обращении к ней, но и какие-то другие действия в рамках, определённых используемой семантикой данных.")]),_c('p',[_vm._v("Как выполняется, например, вставка данных в таблицу с первичным ключом. Сначала проверяется синтаксическая правильность инструкции. Если она подтверждена, СУБД проверяет существование таблицы, наличие столбцов, совместимость типов и пр. Наконец, выясняется, не возникнет ли дублирование ключа, не нарушаются ли другие ограничения целостности. Заметим, что все эти виды активности в инструкции явно не задавались, а были определены семантикой, использованной при создании таблицы.")]),_c('p',[_vm._v("В дальнейшем будет использована в основном модельная семантика, представляемая как расширение теоретико-модельной семантики, определенной через отображение знака на/в некоторую опорную не обязательно чёткую модель.")]),_c('p',[_vm._v("Выделим следующие виды семантик для структурированных моделей:")]),_c('p',[_vm._v("1. Встроенные вендорами в физическую модель. Это типы данных, ограничения целостности, более широко, метаданные. Требуют активности СУБД. Бывают декларативные и процедурные.")]),_c('p',[_vm._v("2. Семантики концептуальной модели, у которых опорная модель это описание бизнес-процессов или их фрагментов. Предполагает аспект в некотором смысле правильного отображения предметной области в базе данных. Активности быть не может, если опорная модель не представлена эксплицитно.")]),_c('p',[_vm._v("3. Семантики логического или физического уровней с опорной моделью в виде некоторого представления структуры данных и аппаратных средств. Позволяют рассматривать аспекты быстродействия и масштабируемости. Активности нет, либо она очень ограничена.")]),_c('p',[_vm._v("4. Семантики отражающие особенности измерений атрибутов или семантики добавленные пользователем. Опорная модель связана с обработкой, а не с хранением данных. Может требовать активности СУБД, но часто интерпретируется человеком.")]),_c('p',[_vm._v("5. Семантики связанные с эмуляцией других моделей данных. Типичный пример – представление деревьев в таблицах. Может потребовать активности.")]),_c('br'),_c('h3',[_vm._v("Модельная семантика")]),_c('br'),_c('p',[_vm._v("Модельная семантика в нашем понимании задаётся через отображения изучаемого объекта на некоторую опорную модель, базисную для изучаемой семантики.")]),_c('p',[_vm._v("Модельная семантика S включает опорную модель M и отображение интерпретации I:")]),_c('p',[_vm._v("S = 〈M, I〉")]),_c('p',[_vm._v("где ")]),_c('p',[_vm._v("M – опорная модель, на/в которую производится отображение; обязательное условие – существенность для выявления семантики.")]),_c('p',[_vm._v("I – отображение интерпретации, в частном случае это биекция.")]),_c('p',[_vm._v("Термин “опорная модель” понимается в самом широком смысле и включает в себя алгебраические модели (тогда говорим о теоретико-модельной семантике), вычислительные модели Э.Х. Тыугу, модели бизнес-процессов, фрагменты этих моделей и многое другое. ")]),_c('p',[_vm._v("Для опорной модели не всегда требуется полнота описания. Могут использоваться, например, фрагменты диаграмм прецедентов, или текстовые описания некоторой части системы или просто со