пом сущности и им же самим (как бы его дубликатом). Концы бинарной связи в ER-модели характеризуются:")]),_c('p',[_vm._v(" – степенью конца связи (сколько экземпляров данного типа сущности должно присутствовать в каждом экземпляре данного типа связи); ")]),_c('p',[_vm._v(" – обязательностью связи (т. е. любой ли экземпляр связываемой сущности должен участвовать в некотором экземпляре данного типа связи).")]),_c('p',[_vm._v("В продвинутых системах могут использоваться имена ролей (имена концов связи), определяющие функции связи по отношению к связываемым сущностям.")]),_c('img',{staticStyle:{"width":"600px"},attrs:{"src":require("../assets/17.png")}}),_c('br'),_c('br'),_c('h3',[_vm._v("Атрибуты. Атомарность.")]),_c('br'),_c('p',[_vm._v("Атрибут это свойство сущности или связи, получаемое путем наблюдения или измерения. Информацию об экземпляре сущности выражают набором нескольких пар “атрибут – значение”. Атрибут принимает одно или несколько значений из некоторого набора. Значения атрибутов обычно принадлежат одному типу, но возможны бестиповые атрибуты. На ER-диаграммах типы атрибутов не указывают. Свойство атомарности атрибута корректно определяется только в рамках выбранной семантики. Атрибут атомарный, если его компоненты не имеют смысла или не должны быть выделены в этой семантике. Атрибут, атомарный в одной семантике, может быть неатомарным в другой.")]),_c('p',[_vm._v("Пример: Если, например, мы обещаем никогда не интересоваться отдельно фамилией именем и отчеством, то атрибут ФИО, то есть “Фамилия, имя, отчество” атомарный. Если эти компоненты нам нужны, то ФИО неатомарный атрибут.")]),_c('p',[_vm._v("Замечание: В моделях данных основанных на реляционной алгебре и исчислениях на кортежах и доменах атрибуты должны быть атомарными. Однако, в современных СУБД над основной моделью имеется второй слой -- XML и/или регулярные выражения -- позволяющий работать с регулярными выражениями и текстами XML, которые на нижнем уровне считаются атомарными, то есть не структурируемыми.")]),_c('br'),_c('h3',[_vm._v("Атрибуты. Атомарность.")]),_c('br'),_c('p',[_vm._v("Некоторые сорта атрибутов (может быть, пересекающиеся!):")]),_c('ul',[_c('li',[_vm._v("Ключевые атрибуты, однозначно определяющие экземпляр сущности. Это ключи первичные, уникальные, альтернативные, суррогатные.")]),_c('li',[_vm._v("Неуникальные ключи.")]),_c('li',[_vm._v("Атрибуты состояния. Не ключевые и не темпоральные.")]),_c('li',[_vm._v("Темпоральные атрибуты задающие моменты или интервалы времени для экземпляра сущности или его компонентов.")]),_c('li',[_vm._v("Атрибуты ресурсов, представляющих какую-то часть сущности-ресурса доступную экземплярам другой сущности (я и 100000 р. в кармане). Хранятся в сущностях-контейнерах.")]),_c('li',[_vm._v("Атрибуты допускающие отсутствие значения (Null).")]),_c('li',[_vm._v("Атрибуты, вычислимые по данным одного или нескольких кортежей, находящихся, быть может, в нескольких отношениях. или атрибуты, связанные отношениями. Пример — нарастающий итог;")]),_c('li',[_vm._v("Атрибуты связанные по смыслу. Один из атрибутов (поясняющий) доопределяет другой. Например, пары “Вес” и “Единица измерения веса”, результат измерения и шкала. Образуется блок атрибутов.")]),_c('li',[_vm._v("Атрибуты, вводимые пользователем и расширяющие семантику данных.")]),_c('li',[_vm._v("Необязательные атрибуты. В полуструктурированных данных часть атрибутов обязательна, а другие атрибуты, не обязательные, могут существовать только у некоторых экземпляров сущности.")]),_c('li',[_vm._v("Атрибуты, заданные на лингвистических переменных. ")])]),_c('br'),_c('p',[_vm._v("Замечание: Существуют концепты без атрибутов. Их содержание выявляется и уточняется через связи.")]),_c('br'),_c('h3',[_vm._v("Блоки атрибутов")]),_c('br'),_c('p',[_vm._v("“Блок” (“блок атрибутов”) это набор атрибутов, объединённых общей семантикой. Пример: первичный ключ из одного или нескольких столбцов")]),_c('p',[_vm._v("Блок имеет трёхчастную структуру:")]),_c('p',[_vm._v("-- первая компонента 